@font-face {
  font-family: "Circe";
  font-weight: 300;
  src: local("Circe-Light"), url("../fonts/Circe-Light.woff2") format("woff2"),
    url("../fonts/Circe-Light.woff") format("woff");
  font-display: swap;
}
